<template>
    <div>
        <button @click="Componentcombine()">Combine Node</button>
        <button @click="switchlevel()">Switch Level</button>
        <p>{{ message }}</p>
        <div ref="chartContainer" :key="componentKey">
        </div>
    </div>
</template>


<script>
import * as d3 from 'd3';
export default {
    name: 'TopLevel',
    data() {
        return {
            links1: null,
            nodes1: null,
            links2: null,
            nodes2: null,
            nodesIN: null,
            linksIN: null,
            simulation: null,
            svgWidth: 1400,
            svgHeight: 800,
            nodeRadius: 16,
            flag: true,
            componentKey: 0,
            selectedNodes: [],
            componentNum: 0,
            message: "Component Level"
        };
    },
    async mounted() {
        try {
            const response1 = await fetch("./assets/festo.json"); // 使用实际的JSON文件路径
            // const response = await fetch("./assets/FB1.json"); // 使用实际的JSON文件路径
            // const response = await fetch("./assets/simpleData.json"); // 使用实际的JSON文件路径
            if (!response1.ok) throw new Error('Network response was not ok');
            const graphData1 = await response1.json();
            const response2 = await fetch("./assets/FB1.json");
            if (!response2.ok) throw new Error('Network response was not ok');
            const graphData2 = await response2.json();
            this.links1 = graphData1.links.map(d => ({ ...d }));
            this.nodes1 = graphData1.nodes.map(d => ({ ...d }));
            this.links2 = graphData2.links.map(d => ({ ...d }));
            this.nodes2 = graphData2.nodes.map(d => ({ ...d }));
            this.linksIN = this.links1;
            this.nodesIN = this.nodes1;
            this.links2 = this.links2.concat(this.links1);
            this.links2 = this.links2.filter(d => {
                // 根据您的要求进行过滤
                return (d.source !== "FB1" && d.target !== "FB1"); // 这里示例删除 ID 为 2 的节点
            });
            this.nodes2 = this.nodes2.concat(this.nodes1);
            this.nodes2 = this.nodes2.filter(d => {
                // 根据您的要求进行过滤
                return d.id !== "FB1"; // 这里示例删除 ID 为 2 的节点
            });
            this.renderChart();
        } catch (error) {
            console.error("Failed to fetch graph data: ", error);
        }
    },
    methods: {
        renderChart() {
            if (this.links1 && this.links2) {
                console.log("data ok!")
                // const width = 1200;
                // const height = 600;
                // const color = d3.scaleOrdinal(d3.schemeCategory10);
                const margin = 120;
                // The force simulation mutates links and nodes, so create a copy
                // so that re-evaluating this cell produces the same result.
                var links = this.linksIN;
                var nodes = this.nodesIN;
                const spacing = 60;
                const maxCnt = Math.max(nodes.filter(node => node.group === 1).length, nodes.filter(node => node.group === 2).length)
                this.svgHeight = spacing * (maxCnt + 1);
                nodes.forEach((d) => {
                    // 把group为1的结点固定在最左侧
                    if (d.group === 1) {
                        d.fx = margin; // 固定 x 坐标在最左侧
                        const group1Nodes = nodes.filter(node => node.group === 1);
                        // const spacing = this.svgHeight / (group1Nodes.length + 1);
                        d.fy = spacing * (group1Nodes.indexOf(d) + 1); // 计算均匀分布的 y 坐标
                        //    leftHeight= spacing *(group1Nodes.length + 1);
                        //     d.fy = spacing * (group1Nodes.indexOf(d) + 1);
                    }
                    // 把group为2的结点固定在最右侧
                    if (d.group === 2) {
                        d.fx = this.svgWidth - margin; // 固定 x 坐标在最右侧
                        const group2Nodes = nodes.filter(node => node.group === 2);
                        const spacing = this.svgHeight / (group2Nodes.length + 1);
                        d.fy = spacing * (group2Nodes.indexOf(d) + 1);  // 计算均匀分布的 y 坐标
                        // rightHeight= spacing *(group2Nodes.length + 1);
                        // d.fy = spacing * (group2Nodes.indexOf(d) + 1);
                    }
                });
                // console.log(nodes)

                // 定义了一个力学模拟，在每个tick步骤的时候运行ticked函数
                this.simulation = d3.forceSimulation(nodes)
                    // 这里用distance属性使连线变长
                    .force("link", d3.forceLink(links).id(d => d.id).distance(50))
                    .force("charge", d3.forceManyBody())
                    .force("collide", d3.forceCollide(this.nodeRadius))
                    // .force("center", d3.forceCenter(this.svgWidth / 2, this.svgHeight / 2))
                    // .force("boundingBox", this.boundingBoxForce())
                    .on("tick", this.ticked);

                const svg = d3.select(this.$refs.chartContainer) //选择文档中的chartContainer元素
                    .append('svg') //添加一个svg元素
                    .attr("width", this.svgWidth)
                    .attr("height", this.svgHeight)
                    .attr("viewBox", [0, 0, this.svgWidth, this.svgHeight])
                    .attr("style", "max-width: 100%; height: auto;");

                // 为箭头定义一个 marker
                svg.append('defs').append('marker')
                    .attr('id', 'arrowhead')
                    .attr('viewBox', '-0 -3 6 6') // 设定 marker 的 viewBox
                    .attr('refX', 3) // 箭头距离线条终点的距离
                    .attr('refY', 0)
                    .attr('orient', 'auto') // 箭头方向与线条一致
                    .attr('markerWidth', 6) // 定义 marker 尺寸
                    .attr('markerHeight', 6)
                    .attr('xoverflow', 'visible') // 防止箭头被剪切
                    .append('svg:path')
                    .attr('d', 'M 0,-3 L 6,0 L 0,3') // 箭头形状
                    .attr('fill', 'black'); // 箭头颜色

                // 创建一个节点组来包含线段和文本
                const link = svg.append("g")//g表示group，一个容器元素，用于将很多svg元素组合到一起
                    .selectAll('g')
                    .data(links)//绑定links数组
                    .join('g');

                // 在线段组中添加线段
                link.append("line")
                    .attr("stroke", "#999")
                    .attr("stroke-opacity", 0.6)
                // .attr("marker-end", "url(#arrowhead)"); // 应用箭头 marker

                // 在线段组中添加文本
                //link.append("text")
                //.attr('class', 'text-link')
                // .attr("text-anchor", "middle") // 水平居中
                //.attr("dominant-baseline", "central") // 垂直居中
                //.style('fill', "black") // 设置字体颜色
                //.style('font-size', '10px') // 设置字体大小
                //.style('font-family', 'Times New Roman') // 设置字体
                //.style('font-style', 'italic')
                //.text(d => d.linkName); // 设置文本内容



                // 创建一个节点组来包含圆和文本
                const node = svg.append("g")
                    .attr("class", "nodes") // 给组添加一个类，以便我们可以引用它
                    .selectAll("g")
                    .data(nodes)
                    .join("g") // 为每个节点数据创建一个'g'元素
                    .join("selectedNodes")
                    .on('click', this.handleNodeClick);

                // node.each(function (d) {
                //     const selection = d3.select(this); // 当前节点的D3选择
                //     // 给属于group6的节点组添加一个矩形节点
                //     if (d.group === 6) {
                //         selection.append("rect")
                //             .attr("width", this.nodeRadius * 2) // 假设你想要矩形宽度为节点半径的两倍
                //             .attr("height", this.nodeRadius * 2) // 假设你想要矩形高度为节点半径的两倍
                //             .attr("x", -this.nodeRadius) // 定位矩形中心
                //             .attr("y", -this.nodeRadius) // 定位矩形中心
                //             .attr("fill", d => this.assignFillColor(d))
                //             .attr("stroke", "black")
                //             .attr("stroke-width", 1.5);
                //     }
                //     else {
                //         selection.append("circle")
                //             .attr("r", this.nodeRadius)
                //             // .attr("fill", d => color(d.group))
                //             .attr("fill", d => this.assignFillColor(d))
                //             .attr("stroke", "black")
                //             .attr("stroke-width", 1.5)
                //     }
                // });

                // 在节点组中添加圆形
                node.append("circle")
                    .attr("r", this.nodeRadius)
                    // .attr("fill", d => color(d.group))
                    .attr("fill", d => this.assignFillColor(d))
                    .attr("stroke", "black")
                    .attr("stroke-width", 1.5)

                // 在节点组中添加文本
                node.append("text")
                    .attr('class', 'text-inner')
                    .attr('x', d => d.x)
                    .attr('y', d => d.y)
                    .attr("text-anchor", "middle") // 水平居中
                    .attr("dominant-baseline", "central") // 垂直居中
                    .style('font-size', '10px') // 设置字体大小
                    .style('fill', d => this.assignTextColor(d))
                    .text(d => d.title); // 将节点数据的id设置为文本内容

                node.each(function (d) {
                    // 给属于group1的节点组中根据条件添加第二个文本，位于圆形的左侧
                    if (d.group === 1) {
                        d3.select(this).append("text")
                            .attr("text-anchor", "end") // 文本靠右侧对齐
                            .attr('class', 'text-left')
                            .attr("dominant-baseline", "central") // 垂直居中
                            .style('font-size', '10px') // 设置字体大小
                            .style('fill', "black") // 设置字体颜色
                            .style('font-family', 'Times New Roman') // 设置字体
                            .style('font-weight', 'bold')
                            .text(d => d.name); // 设置文本内容
                    }
                    // 给属于group2的节点组中根据条件添加第二个文本，位于圆形的右侧
                    if (d.group === 2) {
                        d3.select(this).append("text")
                            .attr("text-anchor", "start") // 文本靠右侧对齐
                            .attr('class', 'text-right')
                            .attr("dominant-baseline", "central") // 垂直居中
                            .style('font-size', '10px') // 设置字体大小
                            .style('fill', "black") // 设置字体颜色
                            .style('font-family', 'Times New Roman') // 设置字体
                            .style('font-weight', 'bold')
                            .text(d => d.name); // 设置文本内容
                    }
                    // // 给属于group2的节点组中根据条件添加第二个文本，位于圆形的右侧
                    // if (d.group === 2) {
                    //     d3.select(this).append("text")
                    //         .attr("text-anchor", "middle") // 文本靠右侧对齐
                    //         .attr('class', 'text-bottom')
                    //         .attr("dominant-baseline", "central") // 垂直居中
                    //         .style('font-size', '10px') // 设置字体大小
                    //         .style('fill', "black") // 设置字体颜色
                    //         .style('font-family', 'Times New Roman') // 设置字体
                    //         .style('font-weight', 'bold')
                    //         .text(d => d.name); // 设置文本内容
                    // }
                });

                // 添加拖拽行为，包含开始、进行和结束时的行为
                node.call(d3.drag()
                    .on("start", this.dragstarted)
                    .on("drag", this.dragged)
                    .on("end", this.dragended));
            }

        },
        Componentcombine() {
            if (this.selectedNodes.length === 0) {
                return
            }
            let Outflag = 0;
            let Inflag = 0;
            for (let i = 0; i < this.nodes1.length; i++) {
                if (this.selectedNodes.indexOf(this.nodes1[i]) === -1) {
                    Outflag = 0;
                    Inflag = 0;
                    for (let j = 0; j < this.links1.length; j++) {
                        if (this.links1[j].source.id === this.nodes1[i].id) {
                            if (this.selectedNodes.indexOf(this.links1[j].target) > -1) {
                                Outflag += 1;
                            }
                        }
                        else if (this.links1[j].target.id === this.nodes1[i].id) {
                            if (this.selectedNodes.indexOf(this.links1[j].source) > -1) {
                                Inflag += 1;
                            }
                        }
                    }
                    if (Inflag > 0 && Outflag > 0) {
                        this.selectedNodes.push(this.nodes1[i]);
                    }
                }
            }
            this.componentNum += 1;
            let Numstr = String(this.componentNum);
            let nameforcom = "Com".concat(Numstr);
            let addele = { "id": nameforcom, "title": "Com", "group": 6 };
            console.log(addele)
            this.nodes1.push(addele);
            let linkscompare = this.links1;
            console.log(this.links1);
            for (let i = 0; i < this.selectedNodes.length; i++) {


                for (let j = 0; j < this.links1.length; j++) {
                    if (this.selectedNodes[i].id === this.links1[j].target.id) {
                        for (let k = 0; k < this.nodes1.length; k++) {
                            if (this.links1[j].source.id === this.nodes1[k].id) {
                                let pertoadd = { "source": this.nodes1[k], "target": addele };
                                linkscompare.push(pertoadd);
                                console.log('find link');
                                linkscompare.splice(j, 1);
                                j--;
                                break;
                            }
                        }
                        continue;
                    }
                    if (this.selectedNodes[i].id === this.links1[j].source.id) {
                        for (let k = 0; k < this.nodes1.length; k++) {
                            if (this.links1[j].target.id === this.nodes1[k].id) {
                                let pertoadd = { "source": addele, "target": this.nodes1[k] };
                                linkscompare.push(pertoadd);
                                console.log('find link');
                                linkscompare.splice(j, 1);
                                break;
                            }
                        }
                    }
                }


                for (let j = 0; j < this.nodes1.length; j++) {
                    if (this.selectedNodes[i].id === this.nodes1[j].id) {
                        this.nodes1.splice(j, 1);
                        console.log('find node');
                        break;
                    }
                }
            }
            this.links1 = linkscompare;
            let flag1 = false;
            for (let i = 0; i < this.nodes1.length; i++) {
                if (this.nodes1[i].group === 2 || this.nodes1[i].group === 4) {
                    continue;
                }
                for (let j = this.links1.length - 1; j > -0.5; j--) {
                    if (this.links1[j].source.id === this.nodes1[i].id) {
                        flag1 = false;
                        for (let k = 0; k < this.selectedNodes.length; k++) {
                            if (this.links1[j].target.id === this.selectedNodes[k].id) {
                                flag1 = true;
                                break;
                            }
                        }
                    }
                    if (flag1) {
                        linkscompare.splice(j, 1);
                    }
                }
            }
            this.links1 = linkscompare;
            console.log(this.links1);
            console.log(this.nodes1);
            if (this.flag) {
                this.clearGraph();
                this.linksIN = this.links1;
                this.nodesIN = this.nodes1;
                this.svgWidth = 1400;
                this.svgHeight = 800;
                this.message = "Component Level";
                this.renderChart();
            }
            this.selectedNodes = [];
        },
        handleNodeClick(_, d) {
            // 点击事件处理逻辑
            console.log('点击了节点:', d);
            let rerend = false;
            let isSelected = false;
            for (let i = 0; i < this.selectedNodes.length; i++) {
                if (this.selectedNodes[i].id === d.id) {
                    isSelected = true;
                    rerend = true;
                    this.selectedNodes.splice(i, 1); // 如果节点已经被选中，从选中列表中移除它
                    break;
                }
            }
            if (!isSelected && (d.group > 2.5 && d.group !== 4)) {
                rerend = true;
                this.selectedNodes.push(d); // 否则，将节点添加到选中列表中
            }
            console.log(this.selectedNodes);
            if (rerend) {
                this.clearGraph();
                this.renderChart();
            }
        },

        clearGraph() {
            d3.select(this.$refs.chartContainer).selectAll('*').remove(); // 清除之前生成的图像元素
        },

        switchlevel() {
            this.clearGraph();
            if (this.flag) {
                this.linksIN = this.links2;
                this.nodesIN = this.nodes2;
                this.flag = false;
                this.svgHeight = 1200;
                this.svgWidth = 2400;
                this.message = "Bottom Level";
                this.renderChart();
            }
            else {
                this.linksIN = this.links1;
                this.nodesIN = this.nodes1;
                this.flag = true;
                this.svgWidth = 1400;
                this.svgHeight = 800;
                this.message = "Component Level";
                this.renderChart();
            }
        },

        ticked() {
            const link = this.$refs.chartContainer.querySelectorAll('line');
            const node = this.$refs.chartContainer.querySelectorAll('circle');
            const textInner = this.$refs.chartContainer.querySelectorAll('.text-inner');
            const textLeft = this.$refs.chartContainer.querySelectorAll('.text-left');
            const textRight = this.$refs.chartContainer.querySelectorAll('.text-right');
            const textLink = this.$refs.chartContainer.querySelectorAll('.text-link');
            const textBottom = this.$refs.chartContainer.querySelectorAll('.text-bottom');

            link.forEach(lk => {
                lk.setAttribute('x1', lk.__data__.source.x);
                lk.setAttribute('y1', lk.__data__.source.y);
                lk.setAttribute('x2', lk.__data__.target.x);
                lk.setAttribute('y2', lk.__data__.target.y);
            });

            node.forEach(nd => {
                nd.setAttribute('cx', nd.__data__.x);
                nd.setAttribute('cy', nd.__data__.y);
            });

            textInner.forEach(td => {
                td.setAttribute('x', td.__data__.x);
                td.setAttribute('y', td.__data__.y);
            });

            textLeft.forEach(tld => {
                tld.setAttribute('x', tld.__data__.x - this.nodeRadius - 20);
                tld.setAttribute('y', tld.__data__.y);
            });
            textRight.forEach(trd => {
                trd.setAttribute('x', trd.__data__.x + this.nodeRadius + 20);
                trd.setAttribute('y', trd.__data__.y);
            });
            textBottom.forEach(trd => {
                trd.setAttribute('x', trd.__data__.x);
                trd.setAttribute('y', trd.__data__.y + this.nodeRadius + 10);
            });
            textLink.forEach(tl => {
                tl.setAttribute('x', 0.5 * (tl.__data__.source.x + tl.__data__.target.x));
                tl.setAttribute('y', 0.5 * (tl.__data__.source.y + tl.__data__.target.y));
            });

        },



        // boundingBoxForce() {
        //     return function (d) {
        //         d.x = Math.max(20, Math.min(1200 - 20, d.x));
        //         d.y = Math.max(20, Math.min(600 - 20, d.y));
        //         // d.x = Math.max(this.nodeRadius, Math.min(this.svgWidth - this.nodeRadius, d.x));
        //         // d.y = Math.max(this.nodeRadius, Math.min(this.svgHeight - this.nodeRadius, d.y));
        //     };
        // },
        dragstarted(event) {
            if (!event.active) this.simulation.alphaTarget(0.3).restart();
            event.subject.fx = event.subject.x;
            event.subject.fy = event.subject.y;
        },

        dragged(event, d) {
            if (d.group === 1 || d.group === 2) {
                event.subject.fy = event.y; // 只更新 y，保持 x 固定
            } else {
                event.subject.fx = event.x;
                event.subject.fy = event.y;
            }

        },

        dragended(event, d) {
            if (!event.active) this.simulation.alphaTarget(0);
            if (d.group !== 1 && d.group !== 2) {
                event.subject.fx = null;
            }
            event.subject.fy = null;
        },

        assignFillColor(d) {
            for (let i = 0; i < this.selectedNodes.length; i++) {
                if (this.selectedNodes[i].id === d.id) {
                    return "#B0B0B0"
                }
            }
            if (d.group === 1) {
                return "#1f77b4"
            }
            if (d.group === 2) {
                return "green"
            }
            if (d.group === 4) {
                return "#fff2cc"
            }
            if (d.group === 5) {
                return "#7f7f7f"
            }
            if (d.group === 6) {
                return "orange"
            }
            else {
                return "white"
            }
        },
        assignTextColor(d) {
            if (d.group === 1 || d.group === 2) {
                return "white"
            }
            else {
                return "black"
            }
        }
    },

    beforeUnmount() {
        // 停止模拟
        if (this.simulation) {
            this.simulation.stop();
        }
    },

}
</script>

<style>
/* 全局样式 */
.button-container {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    position: fixed;
    /* 或者使用 fixed 根据需求 */
    top: 20;
    left: 0;
    right: 0;
}
</style>
